.profile {
  display              : grid;
  grid-template-columns: auto auto 1fr;
  grid-template-rows   : auto auto 1fr;
  grid-gap             : 5px;
}

.profile--image {
  width        : 100px;
  height       : 100px;
  border-radius: 50%;
  grid-area    : 1/1/3/2;
}

.profile--name {
  grid-area : 1/2/2/4;
  align-self: flex-end;
}

.profile--job_title {
  grid-area : 2/2;
  align-self: flex-start;
}

.profile--job_title::after {
  content: ' -';
}

.profile--company {
  grid-area : 2/3;
  align-self: flex-start;
}

.profile--review {
  grid-area   : 3/1/4/-1;
  text-indent : 40px;
  padding-left: 10px
}